import React from 'react';
import DataTable from "../../../../components/data_table/DataTable";
import CopyToClipboard from "@vigosan/react-copy-to-clipboard";
import { Icon } from "semantic-ui-react";



const render_copy = row => (
    <CopyToClipboard
        render={({copy}) => (
            <div onClick={() => copy(row)}>
                <Icon name="copy"/>
            </div>
        )}
    />
)

const columns = [
    {header: "Address", key: "address", content: row => row},
    {header: " ", key: "action", content: render_copy},
];

class Addresses extends React.Component {
    state = {
        data: ["address1", "address2"]
    }
    render = () => (
        <div className="panel custom-table">
            <DataTable
                hideSorting={true}
                data={this.state.data}
                rowSelect={this.rowSelect}
                columns={columns}/>
        </div>
    );
}


export default Addresses;
