import React from 'react';
import { ApiNetwork } from "../../../../network/api";

const columns = [
    {
        title: 'Paid on',
        dataIndex: 'date',
        key: 'date',
    },
    {
        title: 'From height',
        dataIndex: 'from_height',
        key: 'from_height',
    },
    {
        title: 'To Height',
        dataIndex: 'to_height',
        key: 'to_height',
    },
    {
        title: 'Amount (Erg)',
        dataIndex: 'amount',
        key: 'amount',
    },
    {
        title: 'Tx Hash',
        dataIndex: 'tx',
        key: 'tx',
    },
];

class Payout extends React.Component {
    state = {
        "data": [],
        "sorting": "date",
        "sortingDir": "asc",
        "pageIndex": 0,
        "perPage": 10
    };

    sort = (key) => {
        console.log(key);
        this.setState(oldState => {
            const direction = (key === this.state.sorting && oldState.sortingDir === 'asc') ? 'desc' : 'asc';
            return {
                sorting: key,
                sortingDir: direction
            }
        })
    };

    loadPayments = () => {
        ApiNetwork.getPastPayment().then(response => {
            this.setState({data: response});
        });
    };

    componentWillMount() {
        console.log("here")
        this.loadPayments();
    }

    render = () => {
        const data = this.state.data;
        return (
            <div className="panel custom-table">
                <table>
                    <thead>
                    <tr>
                        {columns.map(column => (
                                <th key={column.dataIndex} className="sorted">
                                    <button className="sort-button" onClick={() => this.sort(column.dataIndex)}>
                                        {column.title} &nbsp;
                                        {column.dataIndex === this.state.sorting ? this.state.sortingDir === 'asc' ?
                                            <i className="ep-icon-sorted"/> : <i className="ep-icon-sorted flipped"/> : <i className="ep-icon-sort"/>}
                                    </button>
                                </th>
                            )
                        )}
                    </tr>
                    </thead>
                    <tbody>
                    {data.map(dataRow => {
                        return (
                            <tr key={dataRow.key}>
                                {columns.map(column => <td key={column.dataIndex}>{dataRow[column.dataIndex]}</td>)}
                            </tr>
                        )
                    })}
                    <tr colSpan={columns.length}>
                        <td>
                            <div className="paginator">

                            </div>
                        </td>
                    </tr>
                    </tbody>
                </table>
            </div>
        )
    };
}

export default Payout;
