import React from 'react';
import TableRow from './table-row/TableRow.js';

const dataTable = (props) => {
    return (
        <table>
            <thead>
            <tr>
                {props.detail ? (
                    <td width={40}/>
                ): null}
                {props.columns.map(column => (
                    <th key={column.key} className="sorted">
                        {props.hideSorting ? column.header : (
                        <button className="sort-button"
                                onClick={() => props.sort ? props.sort(column.key) : null}>
                            {column.header} &nbsp;
                            {column.key === props.sorting ? props.sortingDir === 'asc' ?
                                <i className="ep-icon-sorted"/> : <i className="ep-icon-sorted flipped"/> :
                                <i className="ep-icon-sort"/>}
                        </button>
                        )}
                    </th>
                ))}
            </tr>
            </thead>
            <tbody>
            {
                props.data.map((dataRow, index) => (
                    <TableRow
                        key={index}
                        data={dataRow}
                        columns={props.columns}
                        rowSelect={props.rowSelect}
                        detail={props.detail}
                    />
                ))
            }
            {props.footer ? (
                <tr colSpan={props.columns.length}>
                    <td>
                        {this.props.footer}
                    </td>
                </tr>
            ) : null}
            </tbody>
        </table>
    )
};

export default dataTable;
