import React from 'react';
import { ApiNetwork } from "../../../../network/api";

class TwoFA extends React.Component {
    state = {
        qrcode: ''
    }

    enable2FA = () => {
        ApiNetwork.change2Fa().then(data => {
            this.setState({qrcode: data.qrcode})
        })
    }

    render = () => {
        if (this.state.qrcode === '') {
            return (
                <React.Fragment>
                    <div className="text-center">
                        To enable 2fa to change it hit button bellow.
                        <br/>
                        <strong>this operation can not be undone</strong>
                    </div>
                    <br/>
                    <br/>
                    <div className="text-center">
                        <button onClick={this.enable2FA} className="btn blue">Enable/Change 2FA</button>
                    </div>
                </React.Fragment>
            )
        }
        return (
            <React.Fragment>
                <div className="text-center">Two step Authentication enabled for you.</div>
                <div className="text-center">Scan this code for later use</div>
            <div className="text-center">
                <img style={{width: '200px'}}
                     alt="qrcode"
                     src={`data:image/jpeg;base64,${this.state.qrcode}`} />
            </div>
            </React.Fragment>
        )
    };
}

export default TwoFA;
