import React from 'react';
import * as formatter from '../../../../formatter';
import ProxyNetworking from "../../../../network/proxy";
import BarLoader from "react-spinners/BarLoader";

class WalletBox extends React.Component {
    state = {
        amount: 0,
        address: '',
        walletAmount: 0,
        walletLoaded: false,
        loading: false,
        sendTransaction: false,
        paymentMessage: ''
    };

    onChange = (key, value) => {
        this.setState({[key]: value});
    };

    payToAddress = () => {
        this.setState({sendTransaction: true});
        ProxyNetworking.payment(this.state.address, this.state.amount * 1e9).then(response => {
            this.setState({sendTransaction: false, paymentMessage: response.data});
        }).catch(error => {
            this.setState({sendTransaction: false, paymentMessage: error.data});
        });
    }

    loadWalletAmount = (showLoading) => {
        if(showLoading) {
            this.setState({loading: true});
        }
        ProxyNetworking.balance().then(response => {
            this.setState({
                walletAmount: response.data.balance,
                walletLoaded: true,
                loading: false
            })
        }).catch(error => {
            this.setState({loading: false});
        })
    }

    componentDidMount() {
        if(!this.state.walletLoaded && !this.state.loading){
            this.loadWalletAmount(false);
        }
    }

    render() {
        return (
            <div className="split-panel">
                <div className="split-panel-form">
                    <label>
                        Recipient address
                        <input value={this.state.address}
                               onChange={event => this.onChange("address", event.target.value)}/>
                    </label>
                    <label>
                        Amount
                        <input value={this.state.amount}
                               onChange={event => this.onChange("amount", event.target.value)}/>
                    </label>
                    <button className="btn blue" onClick={this.payToAddress}>
                        Send
                        {this.state.sendTransaction ? <BarLoader color="white"/> : null}
                    </button>
                    <div>{this.state.paymentMessage}</div>
                </div>
                <div className="split-panel-text">
                    <h1>Wallet Balance</h1>
                    <h1>{formatter.ergo(this.state.walletAmount)} Ergs</h1>
                    <button className="btn blue" onClick={() => this.loadWalletAmount(true)}>
                        Update
                        {this.state.loading ? <BarLoader color="white"/> : null}
                    </button>
                </div>
            </div>
        )
    };
}

export default WalletBox;
