import React from 'react';
import { loadMenu } from "../../../../store/actions/apiActions";
import { connect } from "react-redux";
import ProxyNetworking from "../../../../network/proxy";
import BarLoader from "react-spinners/BarLoader";

class SaveMnemonic extends React.Component {
    state = {
        password: '',
        loading: false,
        message: ''
    };

    onChange = (event) => {
        this.setState({password: event.target.value});
    };

    submit = () => {
        this.setState({loading: true})
        let method = ProxyNetworking.saveMnemonic
        if(this.props.mnemonicSaved){
            method = ProxyNetworking.loadMnemonic
        }
        method(this.state.password).then(response => {
            this.setState({loading: false});
        }).catch(error => {
            this.setState({loading: false});
        })
    }

    render(){
        console.log(this.props.mnemonicSaved);
        return (
            <div className="split-panel">
                <div className="split-panel-form">
                    <label>
                        Mnemonic password
                        <input type="password" value={this.state.password} onChange={this.onChange}/>
                    </label>
                    <button className="btn blue" onClick={this.submit}>
                        {this.props.mnemonicSaved ? "Load Mnemonic" : "Save Mnemonic"}
                        {this.state.loading ? <BarLoader color="white"/> : null}
                    </button>
                </div>
                {this.props.mnemonicSaved ? (
                    <div className="split-panel-text">
                        You stored your proxy mnemonic to file.
                        <br/>
                        Please load mnemonic to proxy worked
                    </div>
                ) : (
                    <div className="split-panel-text">
                        You must save mnemonic to a file to avoid mnemonic lost.
                        <br/>
                        this mnemonic only used for mining colatoral box
                    </div>
                )}
            </div>
        )
    };
}

const mapStateToProps = state => ({
    mnemonicSaved: state.proxy.node.mnemonicSaved,
});

const mapDispatchToProps = dispatch => ({
    loadMenu: () => dispatch(loadMenu())
});

export default connect(mapStateToProps, mapDispatchToProps)(SaveMnemonic);
