import React from 'react';

class TableRow extends React.Component {
    state = {
        showDetail: false
    }

    toggleDetail = () => {
        this.setState(state => {
            return {...state, showDetail: !state.showDetail};
        })
    }

    getDetailRow = () => {
        if (this.props.detail && this.state.showDetail) {
            const DetailComponent = this.props.detail;
            return (
                <tr>
                    <td colSpan={this.props.columns.length + 1} className="detail">
                        <DetailComponent data={this.props.data}/>
                    </td>
                </tr>
            );
        }
        return null
    }

    render = () => {
        return (
            <React.Fragment>
                <tr key={this.props.data.key}>
                    {this.props.detail ? (
                        <td onClick={this.toggleDetail}>
                            {this.state.showDetail ? <i className="ep-icon-down"/> : <i className="ep-icon-right"/>}
                        </td>
                    ): null}
                    {this.props.columns.map(column => (
                        <td key={column.key}
                            onClick={() => this.props.rowSelect ? this.props.rowSelect(this.props.data) : null}>
                            {column.content ? column.content(this.props.data) :
                                <span>{this.props.data[column.key]}</span>}
                        </td>
                    ))}
                </tr>
                {this.getDetailRow()}
            </React.Fragment>
        )
    };
}

export default TableRow;
