import React from 'react';
import withLayout from '../../../../hoc/with_layout/withLayout';
import { ApiNetwork } from "../../../../network/api";
import PaymentRow from './payment-row/PaymentRow';
import AdminSystemLayout from "../../../../layouts/admin_layout/admin_system_layout/AdminSystemLayout";
import BarLoader from "react-spinners/BarLoader";

class Payment extends React.Component {
    state = {
        "data": [],
        "sorting": "date",
        "sortingDir": "asc",
        "pageIndex": 0,
        "perPage": 10
    };

    loadPayments = () => {
        ApiNetwork.getPayment().then(response => {
            const data = response.map(row => {
                return {...row, enabled: true}
            })
            this.setState({data: data});
        });
    };

    submitPayments = () => {
        this.setState({'loading': true})
        let data = []
        this.state.data.forEach(dataRow => {
            const {enabled, ...submitRow} = dataRow
            if(enabled){
                data.push(submitRow)
            }
        });
        ApiNetwork.postPayment(data).then(response => {
            this.setState({loading: false})
        }).catch(error => {
            this.setState({loading: false})
        })
    }

    componentWillMount() {
        this.loadPayments();
    }

    checkMiner = (index, isChecked) => {
        let data = [...this.state.data];
        data[index].enabled = isChecked;
        console.log(data);
        this.setState({'data': data});
    }

    render() {
        return (
            <div className="panel custom-table">
                <table>
                    <thead>
                    <tr>
                        <td></td>
                        <td>Miner</td>
                        <td>Balance</td>
                        <td>Actual Payment</td>
                    </tr>
                    </thead>
                    <tbody>
                    {this.state.data.map((item, index) => <PaymentRow {...item} saveEnabled={value => this.checkMiner(index, value)}/>)}
                    </tbody>
                    <tfoot>
                    <tr>
                        <td colSpan={4} style={{padding: 10}}>
                            <button className="btn blue" onClick={this.submitPayments} style={{float: "right"}}>
                                Pay to selected
                                {this.state.loading ? <BarLoader color={"white"}/> : null}
                            </button>
                        </td>
                    </tr>
                    </tfoot>
                </table>
            </div>

        )
    };
}

export default withLayout(AdminSystemLayout)(Payment);
