import React from 'react';
import withLayout from "../../../../hoc/with_layout/withLayout";
import * as formatter from '../../../../formatter';
import "react-datepicker/dist/react-datepicker.css";
import 'rc-time-picker/assets/index.css';
import TransactionDetail from './transaction-detail/TransactionDetail.js'
import AdminSystemLayout from "../../../../layouts/admin_layout/admin_system_layout/AdminSystemLayout";
import DataTable from "../../../../components/data_table/DataTable";
import { ApiNetwork } from "../../../../network/api";
import { Icon } from "semantic-ui-react";

const is_confirmed = row => {
    if(row.is_confirmed)
        return <Icon name="check" />
    return <i className="ep-icon-close"/>
}
const columns = [
    {header: "Transaction ID", key: "tx_id"},
    {header: "Amount", key: "total_payment", content: row => {return formatter.ergo(row.total_payment)}},
    {header: "Confirmed", key: "is_confirmed", content: is_confirmed},
    {header: "Timestamp", key: "created_at"},
];

class Transaction extends React.Component {
    state = {
        data: [],
        date: '',
        userId: ''
    };

    loadPayments = () => {
        ApiNetwork.getPastPayment().then(response => {
            this.setState({data: response});
        });
    };

    componentWillMount() {
        console.log("here")
        this.loadPayments();
    }

    render = () => {
        return (
            <div className="panel custom-table">
                <DataTable
                    hideSorting={true}
                    data={this.state.data}
                    rowSelect={this.rowSelect}
                    detail={TransactionDetail}
                    columns={columns}/>
            </div>
        )
    };
}

export default withLayout(AdminSystemLayout)(Transaction);

