import React from 'react';
import MenuBar from "../../../layouts/main_layout/header/menu_bar/MenuBar";
import { ApiNetwork } from "../../../network/api";
import ReCAPTCHA from "react-google-recaptcha";
import { TOKEN_KEY } from "../../../build/constant";
import queryString from 'query-string';
import BarLoader from "react-spinners/BarLoader";


class Login extends React.Component {
    state = {
        username: '',
        password: '',
        recaptcha: '',
        twofa: '',
        siteKey: '',
        loading: false,
        errors: {
            username: '',
            password: '',
            recaptcha_code: '',
            otp_token: '',
            non_field_errors: ''
        }
    };

    saveContent = (key, value) => {
        this.setState({[key]: value});
    };

    redirect = () => {
        const queryParams = queryString.parse(this.props.location.search);
        if (queryParams.next) {
            this.props.history.push(queryParams.next);
        } else {
            this.props.history.push("/admin/")
        }
    };

    componentWillMount() {
        if (localStorage.getItem(TOKEN_KEY)) {
            this.redirect();
        } else {
            ApiNetwork.getLogin().then(data => {
                this.setState({siteKey: data.site_key})
            })
        }
    }

    signIn = (event) => {
        event.preventDefault();
        this.setState({
            loading: true,
            errors: {
                username: '',
                password: '',
                recaptcha_code: '',
                otp_token: '',
                non_field_errors: ''
            }
        })
        ApiNetwork.login(this.state.username, this.state.password, this.state.recaptcha, this.twofa).then(response => {
            localStorage.setItem(TOKEN_KEY, response.token);
            this.setState({loading: false})
            this.redirect();
        }).catch(error => {
            const errors = {
                username: '',
                password: '',
                recaptcha_code: '',
                otp_token: '',
                non_field_errors: ''
            };
            const errorData = error.response.data;
            Object.keys(errors).forEach(key => {
                if (errorData[key] !== undefined) {
                    errors[key] = errorData[key];
                }
            })
            this.setState({loading: false, errors: errors});
            if(this.state.siteKey) {
                this.captcha.reset();
            }
        })
    };

    render() {
        console.log(this.state)
        return (
            <div className="login">
                <div className="header">
                    <MenuBar/>
                    <div className="clear"/>
                </div>
                <div className="content">
                    {this.state.errors.non_field_errors ? (
                        <ul className="red">
                            {this.state.errors.non_field_errors.map((item, index) => {
                                return <li key={index}>{item}</li>
                            })}
                        </ul>
                    ) : null}
                    <form className="with40" onSubmit={this.signIn}>
                        <h1>Sign in</h1>
                        <label>Username</label>
                        <input
                            type="text"
                            className={this.state.errors.username ? "invalid" : ""}
                            value={this.state.username}
                            onChange={event => this.saveContent("username", event.target.value)}
                        />
                        {this.state.errors.username ? this.state.errors.username.map((item, index) => {
                            return <div className="red text-small" key={index}>{item}</div>
                        }) : null}
                        <label>Password</label>
                        <input
                            type="password"
                            className={this.state.errors.password ? "invalid" : ""}
                            value={this.state.password}
                            onChange={event => this.saveContent("password", event.target.value)}
                        />
                        {this.state.errors.password ? this.state.errors.password.map((item, index) => {
                            return <div className="red text-small" key={index}>{item}</div>
                        }) : null}
                        <label>TwoFA (if enabled)</label>
                        <input
                            type="password"
                            value={this.state.twofa}
                            onChange={event => this.saveContent("twofa", event.target.value)}
                        />
                        {this.state.errors.twofa ? this.state.errors.twofa.map((item, index) => {
                            return <div className="red text-small" key={index}>{item}</div>
                        }) : null}
                        {this.state.siteKey ? (
                            <React.Fragment>
                                <div className="button-row">
                                    <ReCAPTCHA
                                        ref={e => (this.captcha = e)}
                                        sitekey={this.state.siteKey}
                                        onChange={value => this.saveContent('recaptcha', value)}
                                    />
                                </div>
                                {this.state.errors.recaptcha_code ? this.state.errors.recaptcha_code.map((item, index) => {
                                    return <div className="red text-small" key={index}>{item}</div>
                                }) : null}
                            </React.Fragment>
                        ): null}
                        <div className="button-row">
                            <button className="btn blue" onClick={this.signIn}>
                                <div style={{marginBottom: "5px"}}> Sign In</div>
                                {this.state.loading ? <BarLoader color={"white"}/> : null}

                            </button>
                        </div>
                    </form>
                </div>
            </div>
        )
    };
}

export default Login;
