import React from 'react';
import { username } from '../../../../../formatter'


const paymentRow = props => (
    <tr>
        <td><input type="checkbox" checked={props.enabled} onChange={e => props.saveEnabled(e.target.checked)}/></td>
        <td>{username(props.miner_id)}</td>
        <td>{props.balance}</td>
        <td>{props.actual_payment}</td>
    </tr>
)

export default paymentRow;
