import axios from 'axios';
import { store } from '../store'

const instance = axios.create({baseURL: "/"});

instance.interceptors.request.use(config => {
    const apiKey = store.getState().proxy.apiKey;
    if (apiKey) {
        config.headers = {"api_key": apiKey};
    }
    return config;
}, error => {

});

class ProxyNetworking {
    static info = () => {
        return new Promise((resolve, reject) => {
            try {
                instance.get("info").then(response => {
                    resolve(response);
                }).catch(error => {
                    console.log(error);
                    reject(error);
                });
            } catch (e) {
                console.log(e);
                reject(e);
            }
        });
    }

    static saveMnemonic = (password) => {
        return new Promise((resolve, reject) => {
            try {
                instance.post("proxy/mnemonic/save", {pass: password}).then(response => {
                    resolve(response);
                }).catch(error => {
                    console.log(error);
                    reject(error);
                });
            } catch (e) {
                console.log(e);
                reject(e);
            }
        });
    }

    static loadMnemonic = (password) => {
        return new Promise((resolve, reject) => {
            try {
                instance.post("proxy/mnemonic/load", {pass: password}).then(response => {
                    resolve(response);
                }).catch(error => {
                    console.log(error);
                    reject(error);
                });
            } catch (e) {
                console.log(e);
                reject(e);
            }
        });
    }

    static payment = (address, amount) => {
        return new Promise((resolve, reject) => {
            try {
                instance.post("/wallet/payment/send", [{
                    address: address,
                    value: amount
                }]).then(response => {
                    resolve(response);
                }).catch(error => {
                    console.log(error);
                    reject(error);
                });
            } catch (e) {
                console.log(e);
                reject(e);
            }
        });
    }

    static balance = () => {
        return new Promise((resolve, reject) => {
            try {
                instance.get("wallet/balances").then(response => {
                    resolve(response);
                }).catch(error => {
                    console.log(error);
                    reject(error);
                });
            } catch (e) {
                console.log(e);
                reject(e);
            }
        });
    }

    static walletStatus = (apiKey = '', callback, errorCallback = error => {
    }) => {
        try {
            const headers = {
                'api_key': apiKey,
            };
            instance.get('wallet/status', {headers: headers}).then(response => {
                callback(response)
            }).catch(error => {
                errorCallback(error);
            })
        } catch (e) {
            console.log(e);
            errorCallback(e);
        }
    };

    static walletLock = (callback, errorCallback = error => {
    }) => {
        try {
            instance.get('wallet/lock').then(response => {
                callback(response);
            }).catch(error => {
                errorCallback(error);
            });
        } catch (e) {
            console.log(e);
            errorCallback(e);
        }
    };

    static walletUnlock = (password, callback, errorCallback = error => {
    }) => {
        try {
            instance.post('wallet/unlock', {pass: password}).then(response => {
                callback(response);
            }).catch(error => {
                errorCallback(error);
            });
        } catch (e) {
            console.log(e);
            errorCallback(e);
        }
    };

    static proxyReload = (callback, errorCallback = error => {
    }) => {
        try {
            instance.post("proxy/config/reload").then(response => {
                callback(response);
            }).catch(error => {
                errorCallback(error);
            })
        } catch (e) {
            console.log(e);
            errorCallback(e);
        }
    };

    static proxyResetStatus = (callback, errorCallback = error => {}) => {
        try {
            instance.post("proxy/status/reset").then(response => {
                callback(response);
            }).catch(error => {
                errorCallback(error);
            })
        } catch (e) {
            console.log(e);
            errorCallback(e);
        }
    }
}

export default ProxyNetworking;
