import React from 'react';
import withLayout from "../../../../hoc/with_layout/withLayout";
import "react-datepicker/dist/react-datepicker.css";
import 'rc-time-picker/assets/index.css';
import AdminSystemLayout from "../../../../layouts/admin_layout/admin_system_layout/AdminSystemLayout";
import DataTable from "../../../../components/data_table/DataTable";
import { ApiNetwork } from "../../../../network/api";
import { Icon } from "semantic-ui-react";

const is_pool = row => {
    if(row.pool)
        return <Icon name="check" />
    return <i className="ep-icon-close"/>
}


const columns = [
    {header: "Height", key: "height"},
    {header: "Timestamp", key: "timestamp"},
    {header: "Transactions", key: "transactionsCount"},
    {header: "Mined by", key: "miner", content: row => {return row.miner.name} },
    {header: "Pool?", key: "pool", content: is_pool},
];

class Transaction extends React.Component {
    state = {
        data: [],
        date: '',
        userId: ''
    };

    loadPayments = () => {
        ApiNetwork.getBlock().then(response => {
            debugger
            this.setState({data: response.results});
        });
    };

    componentWillMount() {
        this.loadPayments();
    }

    render = () => {
        return (
            <div className="panel custom-table">
                <DataTable
                    hideSorting={true}
                    data={this.state.data}
                    rowSelect={this.rowSelect}
                    columns={columns}/>
            </div>
        )
    };
}

export default withLayout(AdminSystemLayout)(Transaction);

